/* importing variables and fonts */
@import "variables.css";
* {
  --appGradients: var(--gradient2blue);
  --appColor: white;

  --inputColor: darkblue;
  --inputColorWrong: red;
  /* --inputBackGround: rgba(0, 90, 170, 0.3); */
  --inputBackGround: rgba(180, 180, 255,0.5);
  --inputBackGroundFocus: rgba(255, 255, 255,0.6);
  --inputBorder:1px solid var(--yellow);
  --inputBoxShadow: 0px 0px 3px 1px var(--shadowColor), inset 0px 0px 4px 1px var(--shadowColor);

  --btnColor: var(--yellow);
  --btnBackGroundColor: var(--blue);
  --btnBorderColor: var(--yellow);
  --btnBoxShadow: 0px 0px 3px 1px var(--shadowColor);
  --btnBoxShadowFocus: 0px 0px 5px 2px var(--shadowColor);

  --englishFont:var(--English);
  --arabicFont:var(--Tajawal);
  --mainFont: var(--Cairo);
  color: var(--appColor);
  font-size: 1em;
}


html,
body,
#root{
  display: grid;
  grid-auto-rows: 100%;
  grid-auto-columns: 100%;
  place-content: center;
  font-family: var(--mainFont);
  font-size: 16px;
  overflow: hidden;
}

.appContainer {
  display: grid;
  width:100vw;
  height:100vh;
  place-content: center;
  place-items: center;
  font-family: var(--arabicFont);
  text-align: center;
  grid-auto-rows: 15vh 78vh 7vh auto;
  grid-auto-columns: 100vw;
  overflow: hidden;
  background-image: var(--appGradients);
}


header {
  background-image: var(--appGradients);
}

main {
  background-image: var(--appGradients);
}

footer {
  background-image: var(--appGradients);
}

li {
  list-style: none;
}

/* buttons START*/
button, input[type=submit] {
  color: var(--btnColor);
  background-color: var(--btnBackGroundColor);
  outline: none;
  border: 0.3em double var(--btnBorderColor);
  border-radius: 10px;
  box-shadow: var(--btnBoxShadow);
  padding: 0.2em 1.2em;
  margin: 0.5em;
  height:auto;
  cursor: pointer;
  display:grid;
  place-items: center;
  display:block;
  appearance: none;
  font-family: var(--mainFont);
  font-size: 1em;
  font-weight: 400;
  text-shadow: black 1px 1px 3px ;
  text-transform: none;
  text-decoration: none;
  letter-spacing: 0.25px;
  line-height: 2.5em;
}

button:hover, input[type=submit]:hover {
  filter: drop-shadow(1px 1px 3px black) blur(1px);
}
button:focus input[type=submit]:focus{
  box-shadow: var(--btnBoxShadowFocus) inset;
}
/* buttons END*/

/* INPUTS START*/
input[type=text],
input[type=password],
input[type=email],
input[type=number],
input[type=search],
input[type=tel],
select
 {
  font-family: var(--mainFont);
  width:80%;
  outline: none;
  text-decoration: none;
  background-color: var(--inputBackGround);
  color: var(--inputColor);
  border: var(--inputBorder);
  box-shadow: var(--inputBoxShadow);
  padding:0.5em 10px;
  line-height: 2em;
  max-height: 2.5em;
  font-size: 1em;
  border-radius: 10px;
  height:auto;
  letter-spacing: 0.25px;
  text-transform: none;
  text-decoration: none;
}

input[type=text]:focus,
input[type=password]:focus,
input[type=email]:focus,
input[type=number]:focus,
input[type=search]:focus,
input[type=tel]:focus
{
  background-color: var(--inputBackGroundFocus);
}

input[type="email"]{
  direction:ltr;
}

input:disabled {
  opacity: 0.4;
  background-color: darkgoldenrod;
}

input::placeholder {
  opacity: 0.3;

}
input::placeholder {
  opacity: 0.3;

}

input::backdrop{
  background-color: blueviolet;
}

select{
  padding-top: 0.25em;
}
select, select > *, select option{
  font-family: var(--mainFont); 
}

select option{
  background-color: var(--blue);
  color: var(--yellow);
}


/* LABEL END*/
label{
  display: block;
  width: 100%;
  line-height: 1.1em;
  word-break: break-word;
  text-align: right;
  margin: 0.2em 1em;
  padding: 0.2em 1em 0px;
  color: var(--blue);
  font-size: 0.8em;
  font-weight: 600;
  letter-spacing: 0.25px;

}
/* LABEL END*/

hr {
  margin-top: 0.1em;
}

table{
  width:100%;
  margin-top:20px;
  direction: rtl;
  text-align:right;
  font-size: small;
}

tbody{
  width:100%;
}

.appHeader {
  grid-row: 1;
  grid-column: 1;
  height: 100%;
  width: 100%;
  display: grid;
  grid-auto-rows: 100%;
  grid-auto-columns: 100%;
  place-content: flex-start;
  place-items: center;
  box-shadow: 0px 2px 5px 2px var(--shadowColor);
}

.appBody {
  grid-row: 2;
  grid-column: 1;
  width: 100%;
  height: 100%;
  display: grid;
  grid-auto-rows: 100%;
  grid-auto-columns: 100%;
  border: var(--appColor) 2px solid;
  box-shadow: 0px 1px 10px 2px var(--shadowColor) inset,
    0px -1px 10px 2px var(--shadowColor) inset;
  overflow: hidden;
}

.appFooter {
  grid-row: 3;
  grid-column: 1;
  display: grid;
  grid-auto-rows: 100%;
  grid-auto-columns: 100%;
  width: 100%;
  height: 100%;
  place-items: center;
  margin: 0px;
  padding: 0px;
  font-family: var(--mainFont);
  font-size: 1em;
}
