.buttonscontainer{
    display: flex;
    height: 100%;
    min-height:65px;
    width: 100%;
    align-items:flex-end;
    justify-items: right;
  }
.button{
    grid-row:1;
    padding:0px;
    margin:0px;
    max-width:60px;
}

.button:hover , .button:focus{
    filter: drop-shadow(1px 1px 3px var(--yellow)) blur(1px);
    box-shadow: 0px 0px 3px 1px var(--shadowColor);
}
