.confheadercontainer{
    grid-row:1;
    grid-column: 1;
    width:100%;
    height:100%;
    display:grid;
    grid-auto-rows: 100%;
    grid-auto-columns: 30% 30% 20% 20%;
    place-items: center;
    margin:0px;
}
.conflogoWrapper{
    grid-row: 1;
    grid-column: 1;
    width:100%;
    height: 100%;
    padding:8px;
    display:grid;
    grid-auto-rows: 100%;
    grid-auto-columns: 100%;
    place-items: center;
}
.searchWrapper{
    grid-row: 1;
    grid-column: 2;
    width:100%;
    height: 100%;
    display:grid;
    grid-auto-rows: 100%;
    grid-auto-columns: 100%;
    place-items: end; 
}

.orglogoWrapper{
    grid-row: 1;
    grid-column: 3;
    width:100%;
    height: 100%;
    place-self: center;
    display:grid;
    grid-auto-rows: 100%;
    grid-auto-columns: 100%;
    place-items: center;
}

.countersWrapper{
    grid-row: 1;
    grid-column: 4;
    width:100%;
    height:100%;
    padding:2px;
    display:grid;
    grid-auto-rows: 100%;
    grid-auto-columns: 100%;
    place-items: center; 
}

.conflogo {
    grid-row:1;
    grid-column: 1;
    height:100%;
    justify-self: right;
  }

.wpulogo {
    grid-row:1;
    grid-column: 1;
    height:100%;
    justify-self: center;
  }

  @media screen and (max-width: 1400px){
    .conflogo {
        max-width: 330px;
    }
  }
    @media screen and (max-width: 1000px){
        .confheadercontainer{
        grid-row:1;
        grid-column: 1;
        width:100%;
        height:100%;
        display:grid;
        grid-auto-rows: 100%;
        grid-auto-columns: 100%;
        place-items: center;
        margin:0px;
    }
    .searchWrapper{
        grid-row: 1;
        grid-column: 1;
        width:100%;
        height: 100%;
        display:grid;
        grid-auto-rows: 100%;
        grid-auto-columns: 100%;
        place-items: end; 
    }
    .conflogo {
        display:none;
    }
    .orglogoWrapper{
        display:none;
    }
    .wpulogo {
        display:none;
    }
  }