.circularMenu {
  position: relative;
  height: var(--xl-space);
  width: var(--xl-space);
  place-self: center;
}

.menuButton,
.menuItem {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  align-items: center;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  box-shadow: 0px 0px 3px 1px rgba(0, 0, 0, 0.5);
}

.menuButton {
  background-color: white;
  background-image: var(--logo);
  background-size: 70%;
  background-clip: border-box;
  background-position: 50%;
  background-repeat: no-repeat;
  transform: scale(1.5);
  z-index: 100;
  border: var(--blue) 5px solid;
}

.menuItem {
  transition: transform 500ms ease;
  color: var(--yellow);
  background-color: var(--blue);
  font-family: 'Tajawal';
  font-size: 1.5em;
  font-weight: bold;
  list-style-type: none;
  display: flex;
  place-content: center;
  place-items: center;
  box-shadow: none;
}

.menuItem span {
  display: flex;
  place-content: center;
  place-items: center;
  place-self: center;
  width: 100%;
  height: 100%;
}


.menuItem img:hover {

   filter: drop-shadow(1px 1px 3px black) blur(1px); 

}

