.appcontents {
  grid-row: 1;
  grid-column: 1;
  width: 100%;
  height: 100%;
  margin: 0px;
  padding-top: 0px;
  display: grid;
  grid-auto-rows: 100%;
  grid-auto-columns: 65% 35%;
  place-items: center;
}

.cardscontainer {
  grid-row: 1;
  grid-column: 1;
  width: 100%;
  height: 100%;
  margin: 0px;
  padding-top: 15px;
  display: grid;
  grid-auto-rows: auto;
  grid-auto-columns: 100%;
  place-content: start;
  overflow: hidden;
  overflow-y: scroll;
  row-gap: 2.5%;
}

.formcontainer {
  grid-row: 1;
  grid-column: 2;
  display: grid;
  grid-auto-rows: 100%;
  grid-auto-columns: 100%;
  width: 100%;
  height: 100%;
  place-items: center;
  overflow: hidden;
}

@media screen and (max-width: 1000px) {
  .appcontents {
    grid-auto-columns: 100%;
  }
  .formcontainer {
    display: none;
    grid-row:unset;
    grid-column: unset;
    grid-auto-rows: unset;
    grid-auto-columns: unset;
    width: 0px;
    height: 0px;
    place-items: center;
  
  }
}
