.headercontainer{
    grid-row:1;
    grid-column: 1;
    width:100%;
    height:100%;
    display:grid;
    grid-auto-rows: 100%;
    grid-auto-columns: 200px auto;
    place-items: center;
    margin:0px;
}

.orglogoWrapper{
    grid-row: 1;
    grid-column: 1;
    width:100%;
    height: 100%;
    display:grid;
    grid-auto-rows: 100%;
    grid-auto-columns: 100%;
    place-items: center;
    padding:6px;
}

.wpulogo {
    grid-row:1;
    grid-column: 1;
    height:100%;
    justify-self: center;
    filter:drop-shadow(2px 2px 3px rgb(70, 70, 70) );
  }

.titleWrapper{
    grid-row:1;
    grid-column: 2;
    display:grid; 
    width:100%;
}
.h1{
    grid-row:1;
    grid-column: 1;
    height:100%;
    font-size: 3.5em;
    justify-self:right;
    color:var(--yellow);
    filter:drop-shadow(2px 2px 5px black )
}