.formcontainer {
  grid-row: 1;
  grid-column: 1;
  display: flex;
  flex-direction: column;
  width:82%;
  height:90%;
  place-items: center;
  border-radius: 2em;
  overflow: hidden;
  box-shadow: 1px 1px 3px 5px var(--shadowColor) inset,0px 0px 3px 2px var(--shadowColor);
  border:var(--blue)  solid 10px;
  background-color: var(--white);
}

.dialog{
  background-color: #bf1650;
  height:300px;
  width:400px;
  align-self: center;
}

.title{
  font-size: 1.6vw;
  font-weight: 400;
  width:100%;
  height:10% ;
  background-color: var(--yellow);
  color:var(--blue);
  box-shadow: 0px 3px 4px 1px var(--shadowColor);
}

.formcards{
  width: 100%;
  height: 100% ;
  cursor: pointer;
  display: grid;
  place-items: center;
  overflow: hidden;
  overflow-y: auto;
  padding:1.2em;
}

.formcard {
    transition: 0.3s;
    width: 90%;
    height:auto;
    border-radius: 1em;
    margin-top: 4px;
    padding: 6px;
  }
  
  .formcard:hover {
    box-shadow: 0px 3px 4px 1px var(--shadowColor);
    transition: 0.3s;
  }

  .star{
      color:crimson;
  }

.formsubmit{
  margin:  1.5em auto 1em;
  max-height: 3em;
  padding: 0em 2.5em;
}

.buttonsContainer{
  width:100%;
  box-shadow: 0px -3px 4px 1px var(--shadowColor);
  background-color: var(--yellow);  
}

.wpup {
  color: #bf1650;
  font-size: smaller;
}

.forminputs{
  width:80%;
  margin-top: 0.2em;
}

.email{
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.formerrorbar{
  margin-top:5px;
  color: crimson;
  word-wrap: break-word;
  overflow-wrap: break-word;
  font-size: 17px;
  font-weight: 600;
  letter-spacing: 0.25px;
  line-height: 36px;
  height: auto;
  text-transform: none;
  border-top: 0px;
  border-right:0px;
  border-left:0px;
  min-width: 250px;
}
