.copyRightFooter{
    grid-row: 1;
    grid-column: 1;
    text-align: center;
    display:block;
    width:250px;
    background-color: white;
    border-top-left-radius: 100%;
    border-top-right-radius: 100%;
    padding-top: 25px;
    box-shadow:0px 0px 5px 0px var(--shadowColor);
}

.h4 {
    font-size: 0.7em;
}
.h3 {
    font-size: 0.5em;
    display:inline-block;
    transform:translateY(-10px);
}
.span {
    font-size: 1.5em;
    font-weight: 100;
}

.h3, .h4, .span {
    margin:0;
    padding:0;
    line-height: 1.2em;
    color:var(--blue);
}