.wpuformcontainer {
  grid-row: 1;
  grid-column: 1;
  height:420px;
  width:420px;
  margin:  auto;
  display:flex;
  flex-direction: column;
  background-color: var(--blue);
  padding: 1.2em;
  direction: rtl;
  border: var(--yellow) 5px solid;
  font-family: var(--Tajawal);
  border-radius: 0.8em;
}

.wpuformlogocontainer{
  display: grid;
  width:100%;
  height:100px;
  margin:0 auto;
  background-color: var(--white);
  place-content: center;
  overflow: hidden;
  border-radius:0.8em 0.8em 0px 0px;
}

.wpulogo{
  grid-row:1;
  grid-column: 1;
  height:100px;
  margin:0 auto;
}

.formtitle{
  color:var(--yellow);
  font-size: 1.5em;
  filter:drop-shadow(1px 1px 5px black);
}

.wpuform {
  display:grid;
  place-content: center;
  margin: 0 auto;
  width:100%;

}

.wpusubform{
  display: grid;
  place-content: center;
  place-items: center;
  width:100%;
}
.wpusubform label{
  font-size: 1em;
  color:white;
  padding: 0.2em;
}
.wpusubform input{
 width:100%;
}

.wpup {
  color: #bf1650;
  font-size: 0.8em;

}

.wpup::before {
  display: inline;
  content: "âš  ";
}



@media screen and (max-width: 767px) {
  .wpuformcontainer {
    padding:10px;
    margin:auto;
    max-width: 420px;
  }
}
