.box {
    grid-row: 1;
    grid-column: 1;
    width: 100%;
    height: 100%;
    display: grid;
    grid-auto-rows: 49% 48%;
    grid-auto-columns: 30% 65% auto;
    place-content: center;
    justify-items: flex-start;
    align-items: flex-start;
    gap: 2%;
    padding-bottom: 2%;
}

.btn {
    grid-row: 2;
    grid-column: 1;
    width:100%;
    max-width: 9em;
    height:min-content;
    padding:0;
    margin-top: -3px;
}

.input {
    grid-row: 2;
    grid-column: 2;
    width:100%;
    max-width: 300px;
}

.label{
  grid-row:1;
  grid-column: 1;
  text-align: right;
  padding-right: 10px;
  margin-right:0.5em;
  font-size: 1.2em;
  color: var(--yellow);
  width:100%;
}

.headermenu{
    grid-row:1;
    grid-column:  2;
    padding-right: 10px;
    margin-right:30px;
    font-size: 1.2em;
    color: var(--yellow);
    width:100%; 
    height:100%;
    max-width: 300px;
    display:flex;
}

@media screen and (max-width: 1200px){
    .btn {
        justify-self: right;
        margin-right: 1em;
    } 
    .label{
        margin-right: 1em;
    }
    .input {
        justify-self: right;
        max-width: 240px;
        margin-right: 1em;
    } 
    .headermenu{
        margin-right: 0px;
    }
}