.card{
  width:80%;
  min-height:200px;
  box-shadow: 0px 0px 3px 2px var(--shadowColor);
  border-radius: 4vmin;
  background-color:var(--white);
  color: var(--color2);
  font-size: 0.8em ;
  display:grid;
  grid-auto-rows: 100%;
  grid-auto-columns: 34% 34% 30%;
  gap:1%;
  direction: rtl;
  margin:0px auto;
  padding:0.5em;
}

.cardcontents{
  grid-row: 1;
  grid-column: 1;
  width:100%;
  height: 100%;
  place-items: center;
  place-self: center;
  border-left: double 5px var(--yellow);
  border-radius: 4vmin;
  padding: 1vh;
  display:grid;
  grid-auto-rows: auto;
  grid-auto-columns: 100%;
}

.cardcontents *{
  color:var(--blue);
}

.h2{
  width:70%;
  background-color: var(--yellow);
  padding: 1vh;
  box-shadow: 0px 0px 3px 2px var(--shadowColor);
  border:2px var(--blue) solid;
  border-radius: 2vmin;
  font-weight: bold;
}

.attendenceWrapper{
  grid-row: 1;
  grid-column: 2;
  display:grid;
  grid-auto-rows: 14% 86%;
}

.days{
  grid-row: 2;
  grid-column: 1;
  display:grid;
  height: 100%;
  grid-auto-columns: 48% 48% ;
  grid-auto-rows: 30% 30% 30%;
  place-content: center;
  place-items: center;
  background-color: var(--white);
  box-shadow: 0px 0px 3px 2px var(--shadowColor);
  gap:4%;
  padding:1em;
}

.f1btn,.f2btn,.cwbtn{
  display:grid;
  place-content: center;
  place-self: center;
  color:var(--white);
  font-weight: bold;
}

.cwbtn{
  width:70%;
  height: 60%;
  font-size: 1.2em ;
}

.f1btn, .f2btn{
  width:80%;
  height: 100%;
  font-size: 0.8em ;
}

.f1btn{
  grid-column: 1;
}
.f2btn{
  grid-column: 2;
}

.email{
  font-family: var(--English);
}
.attendenceTitle{
  color: var(--blue);
  font-size: larger;
}

.controlwrapper{
  grid-row: 1;
  grid-column: 3;
  width:100%;
  height: 100%;
  padding:1em;
  place-self: flex-end;
  justify-self: flex-start;
  border-right: double 5px var(--yellow);
  display:grid;
  border-radius: 4vmin;
}

.errorCard{
  width:75%;
  height:150px;
  display: grid;
  box-shadow: 0px 0px 3px 2px var(--shadowColor);
  color: var(--color2);
  border-radius: 4vmin;
  background-color:var(--color4);
  color: var(--color2);
  font-size: 0.8em ;
  place-content: center;
  place-items: center;
  place-self: center;
}

@media screen and (max-width: 1000px) {
  .card{
    width:96%;
   }
  .controlwrapper{
    padding:0px;
    font-size: smaller;
    width:92%;
    justify-self: flex-end;
  }
  
}
