.appcontents {
  grid-row: 1;
  grid-column: 1;
  width: 100%;
  height: 100%;
  margin: 0px;
  padding-top: 0px;
  display: grid;
  grid-auto-rows: 80% 20%;
  place-items: center;
  position: relative;
}

.guide {
  grid-column: 1;
  grid-row: 2;
  width: 100%;
  height: 100%;
  /*  display:grid;
  place-items: center; */
  /* background-color: black; */
  /* background: var(--gradient7); */
}

.guide h1 {
  font-size: 2em;
  /* color:rgba(0, 90, 170,0.2); */
  /* color:var(--yellow); */
  color: var(--blue);
  background-image: var(--gradient7);

  filter: drop-shadow(2px 2px 5px black);
  /* text-shadow: 1px 1px 5px var(--blue); */
  background-clip: text;
  -moz-background-clip: text;
  -webkit-background-clip: text;
}
.guide h2 {
  width: 200px;
  height: 75px;
  background-color: blueviolet;
}
