.countercontainer {
  grid-row: 1;
  grid-column: 1;
  display: grid;
  height: 100%;
  width: 100%;
  grid-auto-columns: 49% 49%;
  padding: 0px 1%;
  grid-auto-rows: 100%;
  place-items: center;
  place-self: end;
  gap: 2%;
}

.f1,
.f2 {
  grid-row: 1;
  width: 100%;
  height: 100%;
  display: grid;
  grid-auto-rows: 30% 30% 30%;
  grid-auto-columns: 50% 50%;
  place-items: center;
  place-content: center;
  box-shadow: 0px 0px 3px 0px var(--shadowColor), 0px 0px 5px 4px var(--yellow) inset;
  background-color: var(--blue);
  border-radius: 15px;
  border:1px var(--yellow) solid
}
.f2 {
  grid-column: 2;
}
.f1 {
  grid-column: 1;
}

.label {
  grid-column: 1;
  display: grid;
  place-items: center;
  width: 100%;
  height: 100%;
  color: var(--yellow);
  place-self: end;
}
.value {
  grid-column: 2;
  display: grid;
  place-items: center;
  box-shadow: 0px 0px 3px 0px var(--shadowColor) inset;
  border: 1px var(--yellow) solid;
  background-color: var(--white);
  border-radius: 50%;
  width: 36px;
  height: 36px;
  color: var(--inputColor);
}
